.wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding-top: 60px;
    background-image: url('./promo.png');
    background-size: cover;
    background-position: top center;
}

.wrapper p {
    max-width: 890px;
    margin: 0 auto 40px;
    font-family: 'Inter';
    font-weight: 500;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #888A92;
}

.wrapper p:last-child {
    margin-bottom: 0;
}

.wrapper p span {
    color: #fff;
}