.wrapper {
    flex-shrink: 0;
}

.wrapper:hover .imgBg {
    opacity: 1;
}

.imageWrapper {
    position: relative;
}

.img {
    margin-bottom: 2px;
}

.imgBg {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.title {
    /* margin-top: -32px; */
    /* margin-bottom: 6px; */
    /* padding-left: 12px; */

    font-family: 'Inter';
    font-weight: 700;
    font-size: 24px;
    line-height: 100%;
    text-transform: uppercase;

    color: #5498FF;
}

.position {
    margin-top: 0;
    margin-bottom: 8px;
    /* padding-left: 12px; */

    font-family: 'Inter';
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;

    color: #DEDEFD;

    opacity: 0.6;
}

.contacts {
    list-style-type: none;

    margin-top: 0;
    padding-left: 0;

    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.02em;

    color: #FFF;
}

.contact {
    position: relative;
    margin-bottom: 3px;
    cursor: pointer;
}

.contact img {
    display: block;
    width: 23px;
}

.cotnact:last-child {
    margin-bottom: 0;
}
