.wrapper {
    padding-bottom: 210px;
}

.inner {
    padding-top: 238px;
    padding-bottom: 120px;
}

.controls {
    display: flex;
    justify-content: center;
    align-items: center;
}

.control {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 278px;
    height: 74px;

    margin: 0 25px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 90%;
    text-decoration: none;

    border: 1px solid #3A6EE8;
    border-radius: 5px;

    background: #1C2E58;
    color: #D8E2FF;
}

.control:last-child {
    background: #1E2840;
}