.wrapper {
    height: 100vh;
    z-index: 1;
}

.wrapper::after {
    z-index: 2;
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.8;
    background-color: #000;
    pointer-events: none;
    animation: Hide .2s ease-out;
    animation-fill-mode: forwards;
}

@keyframes Hide {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.background {
    z-index: 2;

    position: absolute;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: #000;

    opacity: 0.8;
}

.content {
    position: relative;
    height: 100%;
    z-index: 3;
}

.video {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}